<template>
	<view class="con" :style="{ height: scrollH + 'px' }">
		<view class="tab justify-content-between">
			<view class="i flex" v-for="(item, index) in tabList" :key="index" @tap="open(item.id)">
				<view>{{item.name}}</view>
				<image src="../../static/icon/xiajt.png" mode=""></image>
			</view>
		</view>
		<!-- 状态 -->
		<view v-show="statusFlag" class="openType" @touchmove.stop.prevent="moveHandle" >
			<view :class="item.id == statusIndex ? 'select' : ''" v-for="(item, index) in statusList" :key="index" @tap.stop="changeStatus(item.id)">{{ item.name }}</view>
			<view class="border-b"></view>
		</view>
		<!-- 类型 -->
		<view v-show="typeFlag" class="openType" @touchmove.stop.prevent="moveHandle" >
			<view :class="item.id == typeIndex ? 'select' : ''" v-for="(item, index) in typeList" :key="index" @tap.stop="changeType(item.id)">{{ item.name }}</view>
			<view class="border-b"></view>
		</view>
		<scroll-view scroll-x class="scrollBox">
		<view class="list">
			<view class="items" v-for="(item, index) in List" :key="index">
				<view class="time justify-content-between">
					<view class="left flex">
						<image src="../../static/icon/shijian.png" mode=""></image>
						<view>看铺时间{{item.uTime}}</view>
					</view>
					<view class="right1" v-if="item.orderState == 0">预约待确认</view>
					<view class="right1" v-if="item.orderState == 1">预约已取消</view>
					<view class="right1" v-if="item.orderState == 2">待看铺</view>
					<view class="right1" v-if="item.orderState == 3">预约无效</view>
					<view class="right1" v-if="item.orderState == 4">已看铺待成交</view>
					<view class="right1" v-if="item.orderState == 5">已成交</view>
				</view>
				<view class="flex">
					<image :src="item.resUrl" mode="" class="shopImg"></image>
					<view class="right">
						<view class="tit">{{item.storeTitle}}</view>
						<view class="area">
							<text>{{item.streetTitle}}</text>
							<text class="xian">｜</text>
							<text>{{item.showAddress}}</text>
						</view>
						<view class="rent">
							<text class="rental">{{item.rent}}</text>
							<text class="size">{{item.floorSpace}}㎡</text>
						</view>
					</view>
				</view>
				<view class="remark">备注：{{item.remark}}</view>
				<view class="justify-content-between button">
					<view class="button2" @tap="callMobile(index)">电话咨询</view>
					<view class="button1" @tap="cancel(index)" v-if="item.orderState == 0||item.orderState == 2">取消预约</view>
				</view>
			</view>
		</view>
		
		</scroll-view>
		<view class="shangla">{{loadMore}}</view>
		<view style="height: 30rpx;"></view>
		<uni-popup ref="popup" type="center" :custom="true" :maskClick="false">
			<view class="content">
				<view class="tit">提示</view>
				<view class="content_">您真的确定要取消预约吗？</view>
				<view class="click">
					<view class="cancel" @tap="close">取消</view>
					<view class="comfirm" @tap="comfirm">确认</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import uniPopup from '@/components/uni-popup/uni-popup.vue';

	export default {
		onLoad() {
			this.userId = uni.getStorageSync('userInfo').userId
			this.token = uni.getStorageSync('userInfo').token
			this.getList()
		},
		onReady() {
			uni.getSystemInfo({
				success: (res)=> {
					this.scrollH = res.windowHeight 
				}
			});
		},
		components: {
			uniPopup
		},
		data() {
			return {
				userId:'',
				token:'',
				start:0,
				limit: 10,
				beg: false, //控制发请求
				loadMore:'-上拉加载更多-',
				isloadMore: false,
				List: [],
				index:0,
				tabList: [{
					name: '状态',
					id: 0
				}, {
					name: '类型',
					id: 1
				}],
				scrollH: 0,
				// 状态
				statusFlag: false,
				statusIndex: 0,
				// statusList:[{
				// 	name: '全部',
				// 	id: 0
				// },{
				// 	name: '待确认',
				// 	id: 1
				// },{
				// 	name: '已确认',
				// 	id: 2
				// },{
				// 	name: '已取消',
				// 	id: 3
				// }],
				statusList:[{
					name: '全部',
					id: 0
				},{
					name: '预约待确认',
					id: 1
				},{
					name: '预约已取消',
					id: 2
				},{
					name: '待看铺',
					id: 3
				},{
					name: '预约无效',
					id: 4
				},{
					name: '已看铺待成交',
					id: 5
				},{
					name: '已成交',
					id: 6
				}],
				// 状态
				typeFlag: false,
				typeIndex: 0,
				typeList:[{
					name: '全部',
					id: 0
				},{
					name: '预约',
					id: 1
				},{
					name: '推荐',
					id: 2
				}],
				cancelFlag: false
			};
		},
		methods: {
			onReachBottom() {
				this.getList();
			},
			refreshObject() {
				this.loadMore = '-上拉加载更多-';
				this.start = 0;
				this.isloadMore = false;
				this.List = []
				this.getList()
				
			},
			moveHandle(){
				this.statusFlag = false
				this.typeFlag = false
			},
			cancel(index) {
				this.index = index
				this.$refs.popup.open();
				console.log(this.index)
			},
			close(){
				this.$refs.popup.close();
			},
			// 打开状态选择
			open(id){
				if(id==0){
					this.statusFlag = !this.statusFlag
					this.typeFlag = false
				}else{
					this.statusFlag = false
					this.typeFlag = !this.typeFlag
				}
			},
			// 选择预约状态
			changeStatus(id){
				this.statusIndex = id
				this.statusFlag = false
				this.tabList[0].name = this.statusList[this.statusIndex].name
				this.refreshObject()
			},
			// 选择预约类型
			changeType(id){
				this.typeIndex = id
				this.typeFlag = false
				this.tabList[1].name = this.typeList[this.typeIndex].name
				this.refreshObject()
			},
			// 电话咨询
			callMobile(index){
				uni.makePhoneCall({
					phoneNumber: this.List[index].mobile,
				});
			},
			// 确认取消预约
			comfirm(){
				if(this.cancelFlag) return
				this.cancelFlag = true
				let params = {
					orderId: this.List[this.index].orderId
				}
				uni.showLoading({
					title:"取消中"
				})
				this.$http.post(this.$api.cancelAppointment,{
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res=>{
					if(res.data.code == 0){
						uni.showToast({
							title: '取消成功',
							icon: 'success'
						})
						setTimeout(()=>{
							this.$refs.popup.close();
							this.cancelFlag = false
							this.refreshObject()
						},1000)
					}else{
						this.cancelFlag = false
						this.toast(res.data.msg)
					}
				})
			},
			getList(){
				if(this.isloadMore||this.beg) return
				this.beg = true
				let params = {
					userId: this.userId,
					orderState:this.statusIndex == 0?'':Number(this.statusIndex)-1, 
					type: this.typeIndex == 0?'':this.typeList[this.typeIndex].name,
					start: this.start,
					limit: this.limit,
				}
				uni.showLoading({
					title:"加载中"
				})
				this.$http.post(this.$api.appointmentList,{
					data:params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res=>{
					if(res.data.code == 0){
						this.List = [...this.List, ...res.data.data.list]
						let total = this.start;
						if (!res.data.data.list.length || total >= res.data.data.totalNum || res.data.data.totalNum <= this.limit) {
							this.isloadMore = true;
							this.loadMore = '-没有更多数据了-';
						}
						this.start += 10
						this.beg = false
						uni.hideLoading();
					}else{
						this.beg = false
						this.toast(res.data.msg)
					}
				})
			},
		}
	}
</script>

<style lang="less" scoped>
	.con {
		border-top: 2rpx solid #E8E8E8;
		background: #F9F9F9;

		.tab {
			background: #FFFFFF;
			align-items: center;
			padding: 20rpx 160rpx 20rpx 160rpx;
			box-sizing: border-box;

			.i {
				align-items: center;

				view {
					font-size: 26rpx;
					font-family: PingFang SC;
					font-weight: 400;
					color: #6B6B6B;
				}

				image {
					margin-left: 13rpx;
					width: 28rpx;
					height: 28rpx;
					opacity: 0.6;
				}

				// .transform{
				// 	transform: rotate(90deg);
				// }
			}
		}
		.openType {
			width: 100%;
			z-index: 999;
			height: 100%;
			background-color: rgba(0, 0, 0, 0.5);
			overflow: hidden;
			text-align: center;
			// padding-top: 31rpx;
			box-sizing: border-box;
			// background: #FFFFFF;
			position: fixed;
			top:57rpx;
		
			>view {
				background: #FFFFFF;
				font-size: 28rpx;
				font-family: PingFang SC;
				font-weight: 400;
				color: #4E4E4E;
				padding-bottom: 40rpx;
				box-sizing: border-box;
				&:nth-child(1){
					padding-top: 40rpx;
					box-sizing: borderbox;
				}
			}
		
			.select {
				color: #FF5019;
			}
		
			// .border-b {
			// 	margin-top: 16rpx;
			// 	margin-left: 36rpx;
			// 	padding: 0;
			// 	width: 678rpx;
			// 	height: 2rpx;
			// 	background: #EEEEEE;
			// }
		}

		.list {
			background: #F9F9F9;

			.items {
				background: #FFFFFF;
				box-sizing: border-box;
				padding: 32rpx 36rpx;
				box-sizing: border-box;
				margin-bottom: 20rpx;

				.time {
					margin-bottom: 25rpx;
					align-items: center;

					.left {
						align-items: center;

						image {
							width: 30rpx;
							height: 30rpx;
						}

						view {
							margin-left: 9rpx;
							font-size: 30rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #000000;
						}
					}

					.right1 {
						text-align: right;
						font-size: 26rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #FF4F19;
					}

				}

				.shopImg {
					width: 202rpx;
					height: 150rpx;
					border-radius: 10rpx;
				}

				.right {
					flex: 1;
					margin-left: 36rpx;

					.tit {
						font-size: 32rpx;
						font-family: PingFang SC;
						font-weight: 600;
						color: #343434;
					}

					.area {
						margin-top: 23rpx;

						text {
							font-size: 24rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #909090;

							&:nth-child(3) {
								margin-right: 11rpx;
							}
						}


					}

					.rent {
						margin-top: 23rpx;
						.rental {
							font-size: 30rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #FF6634;
						}

						.size {
							margin-left: 24rpx;
							font-size: 24rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #909090;
						}
					}

				}

				.remark {
					margin-top: 20rpx;
					font-size: 26rpx;
					font-family: PingFang SC;
					font-weight: 400;
					color: #8F8F8F;
				}

				.button {
					margin-top: 28rpx;
					flex-direction: row-reverse;

					.button1 {
						width: 302rpx;
						height: 82rpx;
						line-height: 82rpx;
						border: 2rpx solid #2F2D37;
						border-radius: 12rpx;
						text-align: center;
						font-size: 28rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #2F2D37;
					}

					.button2 {
						width: 302rpx;
						height: 82rpx;
						line-height: 82rpx;
						text-align: center;
						background: #2F2D37;
						border: 2rpx solid #2F2D37;
						border-radius: 12rpx;
						font-size: 28rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #FFFFFF;
					}
				}
			}
		}

		.content {
					background: rgba(246, 246, 246, 1);
					padding-top: 66rpx;
					width: 650rpx;
					border-radius: 20rpx;
					box-sizing: border-box;
					display: flex;
					flex-direction: column;
					font-size: 34rpx;
					text-align: center;
					.tit {
						font-size: 34upx;
						text-align: center;
						font-weight: bold;
					}
					.content_ {
						font-size: 34rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #909090;
						padding: 56rpx 30rpx 63rpx 30rpx;
						box-sizing: border-box;
					}
					.click {
						display: flex;
						border-top: 1rpx solid #dedede;
						> view {
							font-size: 34upx;
							flex: 1;
							text-align: center;
							padding: 38rpx 0;
						}
						.cancel {
							border-right: 1rpx solid #dedede;
							font-weight: 500;
							color: #909090;
						}
						.comfirm {
							color: rgba(52, 52, 52, 1);
						}
					}
				}
	}
</style>
